.header{
    margin: 0 2.5rem;
    padding: 0.5rem 0 ;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_left,.header_right{
    display: flex;
    align-items: center;
}
a > span{
    margin: 0 30px;
    font-size: 1.3rem;
    cursor: pointer;
    color: white;
}
a > span:hover{
   color: #F3CE13; 
}
.header_icon{
    width: 80px;
    cursor: pointer;
}
.user_avatar{
    font-size: 2rem;
}