*{
  margin:0;
  padding: 0;
}
html,body,#root,.App{
  min-height: 100%;
}

.App{
  background-color: black;
  color: white;
  font-family: sans-serif;
}
.header{
    margin: 0 2.5rem;
    padding: 0.5rem 0 ;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_left,.header_right{
    display: flex;
    align-items: center;
}
a > span{
    margin: 0 30px;
    font-size: 1.3rem;
    cursor: pointer;
    color: white;
}
a > span:hover{
   color: #F3CE13; 
}
.header_icon{
    width: 80px;
    cursor: pointer;
}
.user_avatar{
    font-size: 2rem;
}
.posterImage{
    height: 600px;
}
.posterImage>img{
    margin: auto;
    display: block;
    width: 100%;
}
.posterImage_overlay{
    position: absolute;
    padding: 5rem;
    bottom: 0px;
    height: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0,0,0,0),rgb(0,0,0,1));
    opacity: 1;
    transition: opacity 0.3s
}
.posterImage_overlay:hover{
    opacity: 1;

}
.posterImage_title{
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 0.4rem;
    text-align: left;
}
.posterImage_runtime{
    font-size: 2rem;
    margin-bottom: 1rem;
}
.posterImage_rating{
    color:white;
    margin-left: 3rem;
}
.posterImage_description{
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    text-align: left;
    width:50%;
}

.cards{
    display: inline-block;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    margin:0.19rem;
    cursor: pointer;
    min-width: 200px;
    z-index: 0;
    border: 1px solid rgb(99,99,99);
}
.cards:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    z-index: 1000;
    box-shadow: rgba(0,0,0,0.25) 0px 54px 55px, rgba(0,0,0,0.12) 0px -12px 30px,rgba(0,0,0,0.12) 0px 4px 6px,rgba(0,0,0,0.12) 0px 4px 6px;
}
.cards_img{
    height: 300px;
}
.cards_overlay{
    position: absolute;
    padding: 0 1rem 1rem 1rem;
    bottom:0px;
    height: 290px;
    display: flex;
    flex-direction: column;
    width: 85%;
    justify-content: flex-end;
    background-image:linear-gradient(rgb(0,0,0,0),rgb(0,0,0,1)) ;
    opacity: 0;
    transition: opacity 0.2s;
}
.cards_overlay:hover{
    opacity: 1;
}
.cards_title{
    font-weight: 900;
    font-size: 1rem;
    margin-bottom: 0.4rem;
}
.cards_runtime{
    font-size: 0.75rem;
    margin-bottom: 0.25rem;
}
.cards_rating{
    float: right;
}
.cards_description{
     font-style: italic;
     font-size: 0.75rem;
     margin-bottom: 0.25rem;
}
.movie_list{
    padding:0 3rem 3rem 3rem;
}
.list_title{
    font-size: 1.75rem;
    margin:2.5rem;
}
.list_cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.movie {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction:column;
    align-items: center;
}

.movie__intro {
    width: 80%;
}

.movie__backdrop {
    width: 100%;
    height: 500px;
    object-fit:cover;
    object-position: 0 35%;
}

.movie__detail {
    align-items: center;
    width: 75%;
    display: flex;
    position: relative;
    bottom: 225px;
}

.movie__detailLeft {
    margin-right: 30px;
}

.movie__poster {
    width:300px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.86) 0px 22px 40px 6px;
}

.movie__detailRight {
    color: white;
    display: flex;
    flex-direction: column;
    height: 450px;
    justify-content:space-between ;
}

.movie__detailRightTop > div{
    text-shadow: 0px 0px 5px #000000;
    margin-bottom: .5rem;
}

.movie__name {
    font-weight: 600;
    font-size: 3rem;
}

.movie__voteCount {
    margin-left: 1rem;
}

.movie__genres {
    margin: 1.25rem 0;
}

.movie__genre {
    padding: .5rem;
    border: 2px solid white;
    border-radius: 20px;
    margin-right: 1rem;
}


.movie__detailRightBottom {
    margin: 2rem 0;
    flex: 0.8 1;
}

.synopsisText {
    font-size: 1.5rem;
    margin-bottom: 1.25rem;
    font-weight: 600;
    display: flex;
    position: relative;
    align-items: center;
}
.synopsisText > div:last-of-type {
    margin-left: auto;
}

.movie__links {
    position: relative;
    bottom: 120px;
    display: flex;
    justify-content: space-between;
    width: 75%;
}

.movie__heading {
    font-size: 2.2rem;
}

.movie__Button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.8rem 2rem;
    border-radius: 20px;
    cursor: pointer;
    width: 150px;
    color: black;
    font-weight: bold;
}

.movie__homeButton {
    background-color:rgb(255, 0, 0);
    
}

.movie__imdbButton {
    background-color:#f3ce13;
}

.newTab {
    margin-left: 1.4rem;
}

.movie__production {
    width: 85%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 4rem;
}


.movie__productionComapany {
    width: 200px;
    margin: 2rem;
}

.productionCompanyImage {
    border-radius: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: aliceblue;
    height: 300px;

}
.movie_productioncompanyname{
    color:#000000
}

